import React, { Component, PropTypes } from 'react'
import {connect} from 'react-redux'
import ReactCSSTransitionGroup from 'react/lib/ReactCSSTransitionGroup';

class Login extends Component {
    constructor(props, context) {
        super(props, context);
    };
    render() {
        let item  = this.props.status ? <div id="login-holder">
            <div className="login-holder" id="login-part">
                <ul className="uk-tab" data-connect="#uk-login">
                    <li className="section active" data-id="1"><a href="javascript:;"></a></li>
                    <li className="section" data-id="2"><a href="javascript:;"></a></li>
                </ul>
                <ul className="uk-content" id="uk-login">
                    <li className="content" id="login">
                        <div className="validation phone">
                            <input type="text" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                        </div>
                        <div className="cl-10"></div>
                        <div className="validation password">
                            <input type="password" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                        </div>
                        <div className="cl-10"></div>
                        <label className="remember">
                            <input type="checkbox" />
                        </label>
                        <label className="forget">
                            <a href="./store/password.html" className="uk-link-text">?</a>
                        </label>
                        <div className="cl-20"></div>
                        <div className="uk-width-1-1 pl-100">
                            <button className="uk-button uk-button-primary uk-width-1-1 uk-button-large" type="button" id="login-submit"></button>
                        </div>
                        <div className="uk-section-divider">
                            <span></span>
                        </div>
                        <div className="other-login uk-text-center">
                            <div className="icon-weixin" id="weixin">
                            </div>
                        </div>
                    </li>
                    <li className="content" id="register">
                        <div className="phone validation">
                            <input type="text" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                        </div>

                        <div className="cl-10"></div>
                        <div className="uk-width-1-1">
                            <div className="left validation code">
                                <input type="text" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                            </div>
                            <div className="right">
                                <button className="uk-width-1-1 uk-button uk-button-normal uk-button-large" type="button" id="send-code"></button>
                            </div>
                        </div>
                        <div className="cl-10"></div>
                        <div className="password validation">
                            <input type="password" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                        </div>
                        <div className="cl-20"></div>
                        <div className="uk-width-1-1 pl-100">
                            <button className="uk-button uk-button-primary uk-width-1-1 uk-button-large" type="button" id="register-submit"></button>
                        </div>
                        <div className="part">
                            <div className="cl-30"></div>
                            <label className="agree">
                                <input type="checkbox" className="checked" /> <a href="https://api.zhixingche.com/m/agreement" className="uk-link-text" target="_blank"></a>  <a href="https://api.zhixingche.com/m/privacy" className="uk-link-text" target="_blank"></a>
                            </label>
                        </div>
                        <div className="cl-36"></div>
                    </li>
                </ul>

            </div>
            <div className="login-holder" id="password-part">
                <ul className="uk-tab">
                    <li className="section active" data-id="2"><a href="javascript:;" style={{float: 'left',marginLeft: '25px'}}></a></li>
                </ul>
                <ul className="uk-content">
                    <li className="content" style={{display: 'list-item'}}>
                        <div className="cl-10"></div>
                        <div className="password validation">
                            <input type="password" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                        </div>
                        <div className="cl-10"></div>
                        <div className="password-confirm validation">
                            <input type="password" className="uk-width-1-1 uk-form-input uk-form-input-large" placeholder="" />
                        </div>
                        <div className="cl-20"></div>
                        <div className="uk-width-1-1 pl-100">
                            <button className="uk-button uk-button-primary uk-width-1-1 uk-button-large" type="button" id="password-confirm"></button>
                        </div>

                        <div className="cl-36"></div>
                    </li>
                </ul>
            </div>
        </div> : '';
        return (
            <ReactCSSTransitionGroup component="div" transitionName="fadeIn"  transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                {item}
            </ReactCSSTransitionGroup>

        )
    }
}

Login.propTypes = {
    status: PropTypes.bool,
};

function mapStateToProps(state) {
    return {
        status: state.modalStatus.loginModal
    }
}

export default connect(mapStateToProps)(Login)





